    list-style-type: none;
    padding: 0 15px;
    position: relative;
}

.todo-guide {
    padding: 10px;
    transition: all 0.1s ease-in-out;
    display: none;
}

.todo-item {
    border-bottom: 1px solid #ddd;
    padding: 10px;
    font-weight: 600;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
}

.todo-item.blurred {
    filter: blur(2px);
    display: none;
}

.dragging {
    position: absolute;
    z-index: 1000;
    opacity: 0.7;
    width: calc(100% - 20px);
}

.todo-preview {
    opacity: 0.5;
    pointer-events: none;
    position: static;
    z-index: 0;
    border-left: 5px solid #4bd51b;
